import React, { useState } from "react";

const Tour = ({
  mal_id,
  image_url,
  synopsis,
  score,
  title,
  removeAnime,
  url,
}) => {
  const [readMore, setReadMore] = useState(false);
  // console.log(url);
  return (
    <article className="single-tour">
      <img src={image_url} alt={title} />
      <footer>
        <div className="tour-info">
          <h4>{title}</h4>
          <h4 className="rating">Score:{score}</h4>
          <p>
            {readMore ? synopsis : `${synopsis.substring(0, 50)}...`}
            <button className="read-btn" onClick={() => setReadMore(!readMore)}>
              {readMore ? "show less" : "read more"}
            </button>
          </p>
          <a href={url} className="buy">
            ðŸ›’ Buy now
          </a>

          <button className="delete-btn" onClick={() => removeAnime(mal_id)}>
            Not interested
          </button>
        </div>
      </footer>
    </article>
  );
};

export default Tour;
